import size from 'lodash/size'
import { useMemo } from 'react'

const useCompareData = (compareData, keyIndex = 1) => {
  return useMemo(() => {
    if (!size(compareData)) return [null, null]
    let page = 0
    return compareData.reduce(([all, pages], row) => {
      if (row[0]) page += 1
      all[row[keyIndex]] = row
      pages[row[keyIndex]] = page
      return [all, pages]
    }, [{}, {}])
  }, [compareData, keyIndex])
}

export default useCompareData
